:root {
  --button-color: #00cc33;
  --color-base: #fbf6e1;
  --color-accent: #00cc33;
  --color-sub: #ff9900;
}

@font-face {
  font-family: "mplus-1p";
  src: url("font/MPLUS1p-Medium.ttf") format("truetype");
  font-weight: 500;
}

@font-face {
  font-family: "mplus-1p";
  src: url("font/MPLUS1p-Black.ttf") format("truetype");
  font-weight: 700;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "mplus-1p", sans-serif;
  background-color: var(--color-base);
}

button {
  text-align: center;
  background-color: var(--button-color);
  font-size: 30px;
  border-radius: 8px;
}

.container {
  position: relative;
  max-width: 1024px;
  margin: auto;
  font-size: 20px;
  font-weight: 600;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

header {
  width: 100%;
  height: 80px;
  line-height: 80px;
  text-align: center;
  margin-bottom: 27px;
}

h1 {
  font-size: 50px;
  background-color: var(--color-accent);
}

h2 {
  font-size: 30px;
  margin-bottom: 40px;
  display: inline-block;
}

h2::after {
  content: "";
  display: block;
  width: 100%;
  height: 5px;
  background-color: var(--color-accent);
}

.paperContainer {
  width: 67%;
  height: 550px;
  padding: 40px;
  background-color: #fff;
  margin-right: 25px;
  border-radius: 20px;
}

.paper {
  font-size: 100px;
  margin-bottom: 60px;
}

.peper_question {
  margin-right: 0.3em;
}

.paper .answer {
  max-width: 200px;
  border: 5px dotted #ccc;
  text-align: center;
  border-radius: 8px;
}

.judgment {
  text-align: center;
}

.judgment_text {
  font-size: 50px;
  margin-bottom: 50px;
}

.judgment_next {
  height: 80px;
  width: 200px;
  background-color: var(--color-sub);
}

.operationPanel {
  width: 260px;
}

.tenkey {
  width: 100%;
  margin: auto;
}

.tenkey_numbers {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 10px;
}

.tenkey_numbers button {
  width: 80px;
  height: 80px;
}

.tenkey_other {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  gap: 10px;
}

.tenkey_other button {
  height: 80px;
}

.tenkey_other .delete {
  width: 40%;
  background-color: #aaa;
}

.tenkey_other .answer {
  width: 60%;
  background-color: var(--color-sub);
}

footer {
  position: fixed;
  bottom: 0;
  background-color: #fff;
  width: 100%;
  height: 80px;
}

footer ol {
  display: flex;
}

footer ol li {
  display: inline-block;
}